import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import CloseDayButton from '../widget/CloseDayButton';
import ClosingShiftButton from '../widget/ClosingShiftButton';
import HistorySalesButton from '../widget/HistorySalesButton';
import HouseKeepingButton from '../widget/HouseKeepingButton';
import LogoutButton from '../widget/LogoutButton';
import NewOrderButton2 from '../widget/NewOrderButton2';
import OpeningShiftButton from '../widget/OpeningShiftButton';
import SetupPosButton from '../widget/SetupPosButton';
import SyncDataMasterButton from '../widget/SyncDataMasterButton';
import UploadSalesButton from '../widget/UploadSalesButton';

@withRouter
@connect(({ transaction, sync }) => ({
	transaction,
	sync,
	contentStatus: transaction.contentStatus,
	billDate: transaction.billDate
}))
class Options extends Component {
	constructor(props) {
		super(props);
		this.state = {
			modalUpdateSales: false
		};
	}

	handleOk = (d) => {
		const { dispatch } = this.props;

		dispatch({
			type: 'sync/SYNC_DATA_SALES',
			payload: d
		});
	};

	componentWillReceiveProps(np) {
		// const {sync:{loadingUpdateSales}} = this.props;

		if (np.sync.loadingUpdateSales === false) {
			this.setState({
				modalUpdateSales: false
			});
		}
	}

	render() {
		return (
			<div className="container mx-auto p-4 h-screen">
				<div>
					<NewOrderButton2 />
				</div>

				<div className="w-full h-max grid grid-cols-5 mt-2 gap-2">
					<div className="">
						<HouseKeepingButton />
					</div>
					<div className="">
						<SyncDataMasterButton />
					</div>
					<div className="">
						<SetupPosButton />
					</div>
					<div className="">
						<UploadSalesButton />
					</div>
					{/* <div className="">
						<PrintBillButton
							isOpen={this.state?.modalPrintBill}
							hs={(s, v) => this.setState({ [s]: v })}
							onFocus={() => this.handleFocus()}
						/>
					</div> */}
					<div>
						<OpeningShiftButton />
					</div>
					<div>
						<ClosingShiftButton />
					</div>
					<div className="">
						<CloseDayButton />
					</div>
					<div className="">
						<HistorySalesButton />
					</div>
					<div className="">
						<LogoutButton />
					</div>
				</div>
			</div>
		);
	}
}

export default Options;
