import { Button } from 'antd';
import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';

export default function HouseKeepingButton() {
	const history = useHistory();

	const openPage = () => history.push('/pos/house-keeping');

	const onKeyDownGlobal = (e) => {
		if (e.key === 'F2') {
			e.preventDefault();
			openPage();
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDownGlobal);

		return () => {
			document.removeEventListener('keydown', onKeyDownGlobal);
		};
	}, []);

	return (
		<Button className="btn-page bg-transparent border-2 border-red-500 text-red-600" onClick={openPage}>
			Clear Data <kbd className="kbd">f2</kbd>
		</Button>
	);
}
