import { LoadingOutlined } from '@ant-design/icons';
import { Button, Form, Input, Modal } from 'antd';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import LoadingPage from '../../../components/loading/LoadingPage';

export default function UploadSalesButton(props) {
	const dispatch = useDispatch();
	const ref = React.createRef();

	const { loadingUpdateSales: loading = false } = useSelector((state) => state?.sync);
	const [isOpen, setIsOpen] = useState(false);
	const [state, setState] = useState({
		from: new Date(),
		to: new Date(),
		showLoading: false
	});

	const handleUpdateSales = () => {
		dispatch({
			type: 'sync/SYNC_DATA_SALES',
			payload: {
				from: state?.from,
				to: state?.to
			}
		});

		dispatch({
			type: 'transaction/TRANSACTION_BILL_NO'
		});

		setIsOpen(false);
		setState({
			...state,
			showLoading: true
		});
	};

	const onKeyDownGlobal = (e) => {
		if (e.key === 'F9') {
			setIsOpen(true);
		}
	};

	const onKeyDown = (e) => {
		const div = ref && ref.current && ref.current !== null && ref.current;

		if (document.activeElement === div) {
			if (e.key === 'Enter') {
				return handleUpdateSales();
			}

			if (e.key === 'Escape') {
				setIsOpen(false);
			}
		}

		return null;
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDownGlobal);

		return () => {
			document.removeEventListener('keydown', onKeyDownGlobal);
		};
	}, []);

	useEffect(() => {
		if (isOpen && ref && ref.current) {
			ref.current.focus();
		}
	}, [isOpen, ref]);

	return (
		<>
			{loading ? (
				<Button disabled={loading} className="btn-page" onClick={() => setIsOpen(true)}>
					<LoadingOutlined spin className="text-xl" />
					<p>Uploading...</p>
				</Button>
			) : (
				<Button disabled={loading} className="btn-page" onClick={() => setIsOpen(true)}>
					<p>update sales</p>
					<kbd className="kbd">f9</kbd>
				</Button>
			)}

			<Modal
				centered
				open={isOpen}
				onOk={handleUpdateSales}
				onCancel={() => setIsOpen(false)}
				okText="Kirim Data( ENTER )"
				cancelText="Batal( ESC )"
				cancelButtonProps={{
					size: 'large',
					className: 'rounded-md text-sm'
				}}
				okButtonProps={{
					loading: loading,
					size: 'large',
					className: 'rounded-md text-sm font-semibold bg-primary'
				}}>
				<div ref={ref} tabIndex="-1" onKeyDown={onKeyDown} autoFocus>
					<h1 className="text-xl font-semibold uppercase">upload sales</h1>
					<p className="mt-3 text-base leading-snug">
						<span className="text-red-600 font-semibold">Perhatian!!! </span>
						Silahakan pilih tanggal transaksi untuk melakukan upload sales ke cloud. Pilih tanggal yang sesuai dan cek terlebih dahulu
					</p>

					<div className="my-5">
						<Form layout="vertical">
							<Form.Item label="Tanggal Dari" name="date" initialValue={state?.date}>
								<Input
									type="date"
									onChange={(e) => setState({ ...state, from: e.target.value })}
									className="w-full h-14 rounded-md"
								/>
							</Form.Item>
							<Form.Item label="Sampai Tanggal" name="dateTo" initialValue={state?.dateTo}>
								<Input type="date" onChange={(e) => setState({ ...state, to: e.target.value })} className="w-full h-14 rounded-md" />
							</Form.Item>
						</Form>
					</div>
				</div>
			</Modal>

			<LoadingPage isLoading={loading && state?.showLoading} />
		</>
	);
}
