import { Button } from 'antd';
import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import actions from '../../../redux/transaction/actions';

export default function NewOrderButton2(props) {
	const history = useHistory();
	const dispatch = useDispatch();

	const openPage = () => history.push('/pos/kasir');

	const onKeyDownGlobal = (e) => {
		console.log(e);

		if (e.key === 'F4') {
			e.preventDefault();
			openPage();

			dispatch({
				type: actions.TRANSACTION_BILL_NO
			});
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDownGlobal);

		return () => {
			document.removeEventListener('keydown', onKeyDownGlobal);
		};
	}, []);

	return (
		<>
			<Button className="btn-page h-16 rounded-lg" onClick={openPage}>
				<p>+ Order Baru</p>
				<kbd className="kbd">f4</kbd>
			</Button>
		</>
	);
}
