import { Button, Modal } from 'antd';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';

export default function OpeningShiftButton() {
	const app = JSON.parse(localStorage.getItem('app') || '{}');
	const { cashier = {} } = app;
	const isOpeningShift = cashier?.isOpening || false;

	const history = useHistory();
	const [isOpen, setIsOpen] = useState(!isOpeningShift);

	const openPage = (e) => {
		history.push('/pos/opening-shift');
	};

	const onKeyDownGlobal = (e) => {
		if (e.key === 'F7') {
			e.target.blur();
			e.preventDefault();

			openPage();
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDownGlobal);

		return () => {
			document.removeEventListener('keydown', onkeydown);
		};
	}, []);

	return (
		<>
			<Button onClick={openPage} className="btn-page">
				Opening shift
				<kbd className="kbd">f7</kbd>
			</Button>

			<ModalOpeningShift isOpen={isOpen} onClose={() => setIsOpen(false)} />
		</>
	);
}

function ModalOpeningShift({ isOpen, onClose }) {
	return (
		<Modal centered width={500} footer={false} onCancel={onClose} open={isOpen} bodyStyle={{ backgroundColor: '#f8f8f8', borderRadius: '12px' }}>
			<h1 className="text-center font-semibold text-xl text-red-500">PERHATIAN !!!</h1>
			<p className="text-center text-base leading-snug mt-5">
				Anda tidak dapat melakukan transaksi jika anda belum melakukan opening shift/buka kasir. Mohon untuk melakukan opening shift terlebih
				dahulu dengan cara menekan tombol <kbd className="kbd">f7</kbd> atau menutup popup ini. Terima kasih
			</p>
		</Modal>
	);
}
