import { Button, Modal } from 'antd';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';

export default function LogoutButton() {
	const dispatch = useDispatch();
	const history = useHistory();
	const ref = React.createRef();

	const { loading = false } = useSelector((state) => state?.auth);
	const [isOpen, setIsOpen] = useState(false);

	const handleLogout = () => {
		dispatch({
			type: 'auth/LOGOUT_POS',
			history
		});
	};

	const onKeyDownGlobal = (e) => {
		if (e.key === 'Escape') {
			setIsOpen(true);
		}
	};

	const onKeyDown = (e) => {
		const div = ref && ref.current && ref.current !== null && ref.current;

		if (document.activeElement === div) {
			if (e.key === 'Enter') {
				return handleLogout();
			}

			if (e.key === 'Escape') {
				setIsOpen(false);
			}
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDownGlobal);

		return () => {
			document.removeEventListener('keydown', onKeyDownGlobal);
		};
	}, []);

	useEffect(() => {
		if (isOpen && ref && ref.current) {
			ref.current.focus();
		}
	}, [isOpen, ref]);

	return (
		<>
			<Button disabled={loading} loading={loading} className="btn-page" onClick={() => setIsOpen(true)}>
				<p>logout</p>
				<kbd className="kbd">esc</kbd>
			</Button>

			<Modal
				centered
				open={isOpen}
				onOk={handleLogout}
				onCancel={() => setIsOpen(false)}
				okText="Keluar( ENTER )"
				cancelText="Batal( ESC )"
				cancelButtonProps={{
					size: 'large',
					className: 'rounded-md text-sm'
				}}
				okButtonProps={{
					loading: loading,
					size: 'large',
					className: 'rounded-md text-sm font-semibold bg-primary'
				}}>
				<div ref={ref} tabIndex="-1" onKeyDown={onKeyDown} autoFocus>
					<h1 className="text-xl font-semibold uppercase">logout?</h1>
					<p className="mt-3 text-base leading-snug">
						<span className="text-red-600 font-semibold">Perhatian!!! </span>
						Apakah anda yakin ingin keluar dari aplikasi POS saat ini? Semua data transaksi anda yang sedang berlangsung akan dihapus dan
						tidak bisa dikembalikan.
					</p>
				</div>
			</Modal>
		</>
	);
}
