import { Button } from 'antd';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

export default function SyncDataMasterButton() {
	const dispatch = useDispatch();
	const { loadingSync = false } = useSelector((state) => state?.sync);

	const syncData = () => {
		dispatch({
			type: 'sync/SYNC_DATA_MASTER'
		});
	};

	const onKeyDown = (e) => {
		if (e.key === 'F3') {
			e.preventDefault();
			e.target.blur();

			syncData();
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDown);

		return () => {
			document.removeEventListener('keydown', onKeyDown);
		};
	}, []);

	return (
		<Button disabled={loadingSync} loading={loadingSync} className="btn-page" onClick={syncData}>
			<p>sync data</p>
			<kbd className="kbd">f3</kbd>
		</Button>
	);
}
