import { Button } from 'antd';
import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';

export default function CloseDayButton(props) {
	const history = useHistory();
	const { loadingDetail = false } = useSelector((state) => state?.transaction);

	const onKeyDown = (e) => {
		if (e.key === 'End') {
			history.push('/pos/kasir/closing');
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDown);

		return () => {
			document.removeEventListener('keydown', onKeyDown);
		};
	}, []);

	return (
		<Button disabled={loadingDetail} loading={loadingDetail} className="btn-page col-span-2" onClick={() => history.push('/pos/kasir/closing')}>
			<p>Closing Day</p>
			<kbd className="kbd">end</kbd>
		</Button>
	);
}
